import React, { useEffect, useState } from 'react'
import Container from 'react-bootstrap/Container'
import Table from 'react-bootstrap/Table'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import { LinkContainer } from 'react-router-bootstrap'
import { useRouteMatch } from 'react-router-dom'

import DeviceTableItem from '../../components/layout/DeviceTableItem'
import { getAllDevices } from '../../integration/Integration'
import { POLLING_INTERVAL_DEVICE_LIST } from '../../common/system-wide-variables'

/** TODO-OPT Query parameters per il sorting
 *
 */

const Devices = () => {
  const match = useRouteMatch()

  const [devices, setDevices] = useState()
  const [devicesToRender, setDevicesToRender] = useState()

  useEffect(() => {
    const getDevices = async () => {
      const devices = await getAllDevices()

      if (!devices.isError) {
        setDevices(devices.data)
      } else {
        console.error('Errore nel recupero dei dispositivi:', devices.data.message)
      }
    }

    getDevices()

    const intervalId = setInterval(() => {
      console.log('Polling device list...')
      getDevices()
    }, POLLING_INTERVAL_DEVICE_LIST)

    return () => clearInterval(intervalId)
  }, [])

  useEffect(() => {
    if (devices) {
      setDevicesToRender(
        devices.map(device => {
          return <DeviceTableItem device={device} key={device.id} />
        })
      )
    }
  }, [devices])

  return (
    <Container>
      <Row>
        <Col>
          <h3 className='iedx-home-title'>Gestione Dispositivi</h3>
          <p className='iedx-home-subtitle'>
            In ultricies fermentum aliquet. Pellentesque dui magna, condimentum non ullamcorper at, cursus in sem. Nunc condimentum, purus
            ac sagittis ultricies, metus leo pharetra mi, non vehicula felis elit et nisi.
          </p>
        </Col>
      </Row>
      <div className='iedx-table-container'>
        <div className='iedx-table-header'>
          <p className='iedx-table-name'>Lista dei dispositivi</p>
          <LinkContainer to={`${match.url}/newDevice`}>
            <Button className='iedx-add-button'>Aggiungi Dispositivo</Button>
          </LinkContainer>
        </div>
        <Table striped hover>
          <thead>
            <tr>
              <th>Nome</th>
              <th>Descrizione</th>
              <th>Stato</th>
            </tr>
          </thead>
          <tbody>{devicesToRender}</tbody>
        </Table>
      </div>
    </Container>
  )
}

export default Devices
