import React, { useEffect, useState } from 'react'
import Container from 'react-bootstrap/Container'
import Table from 'react-bootstrap/Table'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import { LinkContainer } from 'react-router-bootstrap'
import { useRouteMatch } from 'react-router-dom'

import PoisTableItem from '../../components/layout/PoisTableItem'
import { getAllPois } from '../../integration/Integration'

/** TODO-OPT Query parameters per il sorting
 *
 */

const Pois = () => {
  const match = useRouteMatch()

  const [pois, setPois] = useState()
  const [poisToRender, setPoisToRender] = useState()

  useEffect(() => {
    const getPois = async () => {
      const pois = await getAllPois()

      if (!pois.isError) {
        setPois(pois.data)
      } else {
        console.error('Errore nel recupero dei dispositivi:', pois.data.message)
      }
    }

    getPois()
  }, [])

  useEffect(() => {
    if (pois) {
      setPoisToRender(
        pois.map(poi => {
          return <PoisTableItem poi={poi} key={poi.id} />
        })
      )
    }
  }, [pois])

  return (
    <Container>
      <Row>
        <Col>
          <h3 className='iedx-home-title'>Gestione Punti di interesse</h3>
          <p className='iedx-home-subtitle'>
            In ultricies fermentum aliquet. Pellentesque dui magna, condimentum non ullamcorper at, cursus in sem. Nunc condimentum, purus
            ac sagittis ultricies, metus leo pharetra mi, non vehicula felis elit et nisi.
          </p>
        </Col>
      </Row>
      <div className='iedx-table-container'>
        <div className='iedx-table-header'>
          <p className='iedx-table-name'>Lista dei Punti di interesse</p>
          <LinkContainer to={`${match.url}/newDevice`}>
            <Button className='iedx-add-button'>Aggiungi POI</Button>
          </LinkContainer>
        </div>
        <Table striped hover>
          <thead>
            <tr>
              <th>Nome</th>
              <th>Descrizione</th>
              <th>Stato</th>
            </tr>
          </thead>
          <tbody>{poisToRender}</tbody>
        </Table>
      </div>
    </Container>
  )
}

export default Pois
