import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Spinner from 'react-bootstrap/Spinner'

import DeviceForm from '../../components/form/DeviceForm'
import { getSingleDevice } from '../../integration/Integration'
import { POLLING_INTERVAL_DEVICE } from '../../common/system-wide-variables'

const DeviceDetail = () => {
  const params = useParams()
  const [currentDevice, setCurrentDevice] = useState()
  const [pollingDevice, setPollingDevice] = useState()

  // Recupera il dettaglio del device da passare al form
  useEffect(() => {
    const getDevice = async () => {
      const device = await getSingleDevice(params.parDeviceId)

      if (!device.isError) {
        setCurrentDevice(device.data)
      } else {
        console.error('Errore nel recupero del dispositivo:', device.data.message)
      }
    }

    getDevice()
  }, [params.parDeviceId])

  // Polling del dettaglio del device per aggiornare in caso di modifiche concorrenti
  useEffect(() => {
    let intervalId
    let isError = false

    if (currentDevice) {
      const getDevice = async () => {
        const device = await getSingleDevice(params.parDeviceId)

        if (!device.isError) {
          setPollingDevice(device.data)
        } else {
          isError = true
          console.error('Errore nel recupero del dispositivo:', device.data.message)
          setPollingDevice({ warning: `Il device ${currentDevice.name}  stato eliminato` })
        }
      }

      intervalId = setInterval(() => {
        if (isError) {
          clearInterval(intervalId)
        }
        console.log('Polling device...')
        getDevice()
      }, POLLING_INTERVAL_DEVICE)
    }

    if (intervalId) {
      return () => clearInterval(intervalId)
    }
  }, [params.parDeviceId, currentDevice])

  const outputToRender = currentDevice ? (
    <div className='iedx-LayoutWrapper'>
      <Container>
        <h3 className='iedx-home-title'>Dettaglio Dispositivo</h3>
        <DeviceForm currentDevice={currentDevice} pollingDevice={pollingDevice} />
      </Container>
    </div>
  ) : (
    <Spinner animation='grow' />
  )

  return (
    <Row>
      <Col>{outputToRender}</Col>
    </Row>
  )
}

export default DeviceDetail
