import React from 'react'
import { useRouteMatch } from 'react-router-dom'
import { LinkContainer } from 'react-router-bootstrap'
import Badge from 'react-bootstrap/Badge'
import { ACTIVE } from '../../common/system-wide-variables'

const PoisTableItem = props => {
  const match = useRouteMatch()

  const { name, note, status, id } = props.poi

  const statusToRender = status === ACTIVE.value ? (
    <Badge pill bg='success'>
      Attivo
    </Badge>
  ) : (
    <Badge pill bg='danger'>
      Non attivo
    </Badge>
  )

  return (
    <LinkContainer to={`${match.url}/${id}`}>
      <tr>
        <td>{name}</td>
        <td>{note}</td>
        <td>{statusToRender}</td>
      </tr>
    </LinkContainer>
  )
}

export default PoisTableItem
